const moment = require("moment");
const DEFAULT_URGENCY_LEVEL = 50;
const getNextHourFormatted = () => {
	const d = new Date();
	d.setHours(d.getHours()+1);
	d.setMinutes(0);
	return moment(d).format("YYYY-MM-DD HH:mm");
}

const formatDate = (dateText) => {
	return moment(dateText);
};

const getTasksSortedByDate = (command, index, commands, scope) => {
	// @TODO:
	const output = {};
	const frequencyTasks = {};
	Object.keys(scope.commandState.tasks).forEach((taskName) => {
		const task = scope.commandState.tasks[taskName];
		if(!task.attributes) {
			return;
		}
		const attrs = scope.computeAttributes(task.attributes);
		if("repetir" in attrs) {
			frequencyTasks[task.title] = task;
		} else if("desde" in attrs) {
			return frequencyTasks[task.title] = task;
		} else if("hasta" in attrs) {
			return frequencyTasks[task.title] = task;
		} else if("fecha" in attrs) {
			if(false
				|| (attrs.fecha.indexOf(",") !== -1)
				|| (attrs.fecha.indexOf("..") !== -1)
				|| (attrs.fecha.indexOf("*") !== -1)) {
				return frequencyTasks[task.title] = task;
			}
			const fecha = formatDate(attrs.fecha);
			if(fecha.isValid()) {
				const year = fecha.get("year");
				const month = fecha.get("month");
				const date = fecha.get("date");
				const hour = fecha.get("hour");
				const minute = fecha.get("minute");
				const key = `${date}/${month}/${year}`;
				if(!(key in output)) {
					output[key] = [];
				}
				output[key].push(task);
			}
		}
	});
	/*
	// @TODO: add repeated tasks from the first date until the last date, or 1 week after and 1 week before by default.
	Object.keys(frequencyTasks).forEach((taskName) => {
		const task = frequencyTasks[taskName];
		// @TODO: develop all frequencies in order to bla bla bla...
	});
	//*/
	return output;
};
const getTasksSortedByUrgency = (command, index, commands, scope) => {
	// @TODO:
	const output = {};
	Object.keys(scope.commandState.tasks).forEach((taskName) => {
		const task = scope.commandState.tasks[taskName];
		if(!task.attributes) {
			return;
		}
		const attrs = scope.computeAttributes(task.attributes);
		if(!(attrs.urgencia in output)) {
			output[attrs.urgencia] = [];
		}
		output[attrs.urgencia].push(task);

	});
	// @TODO: add repeated tasks from the first date until the last date, or 1 week after and 1 week before by default.
	// @TODO: regenerate the output:
	//  - adding the keys in order
	//  - sorting the items inside each object
	return output;
};


const newCommands = {};
newCommands["crear tarea"] = (command, index, commands, scope) => {
	let wasFound = false;
	if(!(command.title in scope.commandState.tasks)) {
		wasFound = false;
		if(!Array.isArray(command.attributes)) {
			command.attributes = [];
		}
		const computedAttributes = scope.computeAttributes(command.attributes);
		if(!("urgencia" in computedAttributes)) {
			command.attributes.push({key:"urgencia",value:DEFAULT_URGENCY_LEVEL});
		}
		if(!("fecha" in computedAttributes)) {
			command.attributes.push({key:"fecha",value:getNextHourFormatted()});
		}
		scope.commandState.tasks[command.title] = Object.assign({}, command, {done: false});
	} else {
		wasFound = true;
	}
	if((index+1) === commands.length) {
		if(wasFound) {
			scope.commandError = new Error("Tarea ya creada: " + command.title);
			throw scope.commandError;
		}
		scope.selected.task = scope.commandState.tasks[command.title];
		scope.selected.view = "ver tarea";
	}
};
newCommands["añadir tarea"] = newCommands["crear tarea"];
newCommands["registrar tarea"] = newCommands["crear tarea"];
