newCommands["+t"] = newCommands["crear tarea"];
newCommands["t+"] = newCommands["crear tarea"];
newCommands["editar tarea"] = (command, index, commands, scope) => {
	console.log("Editando tarea: ", command);
	let hasAttributes = false;
	const wasFound = (command && command.title && (command.title in scope.commandState.tasks));
	if(wasFound) {
		Object.keys(command).forEach(key => {
			if((key !== "attributes") && (command[key] !== null)) {
				scope.commandState.tasks[command.title][key] = command[key];
			} else if(key === "attributes") {
				hasAttributes = true;
			}
		});
	}
	if((index+1) === commands.length) {
		if(!wasFound) {
			scope.commandError = new Error("Tarea no creada: " + command.title);
			throw scope.commandError;
		} else {
			if(hasAttributes) {
				scope.commandState.tasks[command.title].attributes = [...scope.commandState.tasks[command.title].attributes, ...command.attributes];
			}
		}
		scope.selected.task = scope.commandState.tasks[command.title];
		scope.selected.view = "ver tarea";
	}
};
newCommands["modificar tarea"] = newCommands["editar tarea"];
